import { useEffect, useState } from 'react';
import ReCAPTCHA from "react-google-recaptcha";

const storageKey = 'fa15302a';

export default function EngageForm(props) {
    const [counterValue, setCounterValue] = useState(0);
    const [hasPassedRecaptcha, setHasPassedRecaptcha] = useState(false);
    const [hasCounted, setHasCounted] = useState();

    useEffect(() => {
        setHasCounted(window.localStorage.getItem(storageKey));
        fetch('https://api.countapi.xyz/get/boycott-engage/fa15302a-2162-44dc-9e56-69ead0d6477d')
            .then((response) => response.json())
            .then((data) => setCounterValue(data.value));
    }, [])

    function handleEngageOne(e) {
        e.preventDefault();

        if (!hasCounted) {
            fetch('https://api.countapi.xyz/hit/boycott-engage/fa15302a-2162-44dc-9e56-69ead0d6477d')
                .then((response) => response.json())
                .then((data) => setCounterValue(data.value));

            setHasCounted('true');
            window.localStorage.setItem(storageKey, 'true');
        }
    }

    return (
        <div className="engage-container">
            <div className="counter">
                <span className="counter-value">{counterValue}</span> personnes s'engagent a boycotter.
            </div>

            {!hasCounted ?
            <div className="vote-container">
                <div className="recaptcha-container">
                    <ReCAPTCHA
                        theme="dark"
                        sitekey="6Lcs6RkiAAAAAI5hY1-xqqoHs33ph6myyzOXL6-J"
                        onChange={() => setHasPassedRecaptcha(true)}
                    />
                </div>
                <form className="vote-form">
                    <button
                        type="button"
                        onClick={handleEngageOne}
                        disabled={!hasPassedRecaptcha}
                    >
                        Je m'engage
                    </button>
                </form>
            </div>
            :
                <p className="success-msg">
                    Merci de t'être engagé, maintenant fais-le savoir autour de toi.
                </p>
            }
        </div>
    )
}
