import {fabric} from "fabric";

const originalRender = fabric.Textbox.prototype._render;
fabric.Textbox.prototype._render = function (ctx) {
    originalRender.call(this, ctx);

    if (!this.hideBorder && !this.active) {
        const w = this.width;
        const h = this.height;
        const x = -this.width / 2;
        const y = -this.height / 2;
        const stroke = ctx.strokeStyle;
        const borderPadding = 4;

        ctx.beginPath();
        ctx.setLineDash([5, 10]);
        ctx.lineWidth = 2;
        ctx.moveTo(x - borderPadding, y - borderPadding);
        ctx.lineTo(x + w + borderPadding, y - borderPadding);
        ctx.lineTo(x + w + borderPadding, y + h + borderPadding);
        ctx.lineTo(x - borderPadding, y + h + borderPadding);
        ctx.lineTo(x - borderPadding, y + borderPadding);
        ctx.closePath();

        ctx.strokeStyle = 'lightgrey';
        ctx.stroke();
        ctx.strokeStyle = stroke;
    }
}
fabric.Textbox.prototype.cacheProperties = [
    ...fabric.Textbox.prototype.cacheProperties,
    'active',
    'hideBorder'
];

fabric.Image.prototype.toObject = (function (toObject) {
    return function () {
        return fabric.util.object.extend(toObject.call(this), {
            src: `${this.getSrc().split('1234')[1]}`
        });
    };
})(fabric.Image.prototype.toObject);

